:root {
  --yellow: hsl(47, 88%, 63%);
  --white: hsl(0, 0%, 100%);
  --gray500: hsl(0, 0%, 42%);
  --gray900: hsl(0, 0%, 7%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Figtree", sans-serif;
}

body {
  font-size: 16px;
  font-weight: 500;
  color: var(--gray900);
  background-color: var(--yellow);
}

.card-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.card {
  width: 25vw;
  padding: 1.75rem;
  background-color: var(--white);
  border: 2px var(--gray900) solid;
  border-radius: 30px;
  box-shadow: 10px 10px var(--gray900);
}

.background {
  width: 100%;
  object-fit: contain;
  border-radius: 20px;
  margin-bottom: 1.5rem;
}

.tag {
  width: max-content;
  background-color: var(--yellow);
  color: var(--gray900);
  font-weight: 800;
  padding: 0.5rem 0.75rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.published {
  color: var(--gray900);
  margin-bottom: 1.25rem;
}

.title {
  font-size: 1.85rem;
  font-weight: 800;
  margin-bottom: 1.25rem;
  background-color: var(--white);
  border: none;
}

.title:hover {
  cursor: pointer;
  color: var(--yellow);
}

.text {
  font-size: 1.2rem;
  color: var(--gray500);
  margin-bottom: 2rem;
}

.avatar-wrapper {
  display: flex;
  align-items: center;
}

.avatar {
  height: 2rem;
  margin-right: 1rem;
}

.name {
  font-size: 1rem;
  font-weight: 800;
}

@media screen and (max-width: 991px) {
  * {
    font-size: 12px;
  }

  .card {
    width: 85%;
  }

  .published {
    font-size: 0.85rem;
  }

  .title {
    font-size: 1.5rem;
  }

  .text {
    font-size: 1rem;
    line-height: 1.3;
  }
}
